.wp-block-create-block-hetas-block-hot-topic {
  position: relative; }
  .wp-block-create-block-hetas-block-hot-topic a.href::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3; }
  .wp-block-create-block-hetas-block-hot-topic a.href {
    text-decoration: none; }
  .wp-block-create-block-hetas-block-hot-topic a ~ .colored {
    mix-blend-mode: overlay;
    z-index: 1;
    transition: all .5s ease-in-out;
    display: block;
    width: 100%;
    height: 100%; }
  .wp-block-create-block-hetas-block-hot-topic a ~ .colored ~ .centered {
    transition: all .5s ease-in-out; }
  .wp-block-create-block-hetas-block-hot-topic a:hover ~ .hetas-orange ~ .centered {
    background-color: var(--hetas-orange); }
  .wp-block-create-block-hetas-block-hot-topic a:hover ~ .colored.hetas-orange {
    background-color: var(--hetas-orange); }
  .wp-block-create-block-hetas-block-hot-topic a:hover ~ .hetas-green ~ .centered {
    background-color: var(--hetas-green); }
  .wp-block-create-block-hetas-block-hot-topic a:hover ~ .colored.hetas-green {
    background-color: var(--hetas-green); }
  .wp-block-create-block-hetas-block-hot-topic a:hover ~ .hetas-red ~ .centered {
    background-color: var(--hetas-red); }
  .wp-block-create-block-hetas-block-hot-topic a:hover ~ .colored.hetas-red {
    background-color: var(--hetas-red); }
  .wp-block-create-block-hetas-block-hot-topic a ~ img {
    transition: all .5s ease-in-out; }
    .wp-block-create-block-hetas-block-hot-topic a ~ img.noclass {
      filter: grayscale(1); }
  .wp-block-create-block-hetas-block-hot-topic .centered {
    position: absolute;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    text-align: center;
    left: 50%;
    width: 80%;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 2rem;
    font-size: 1.75em;
    color: #fff;
    z-index: 3; }
